import React from "react";
import { Helmet, HelmetProvider } from "react-helmet-async";

import Image from "./components/Images/thumbnail.jpg";

function Head() {
    return (
        <HelmetProvider>
            <Helmet>
                <title>B.Parweez</title>
                <meta property="og:title" content="B.Parweez" />
                <meta property="og:image" content={Image} />
                <meta
                    property="og:description"
                    content="Baber Parweez - Professional Web Developer"
                />
                <meta
                    property="og:url"
                    content="https://baberparweez.github.io/"
                />
            </Helmet>
        </HelmetProvider>
    );
}

export default Head;
